












import uniPassword from '@/components/uni-password/uni-password.vue'
import uniIcon from '@/components/i-icon/i-icon.vue'
import uniKeyboard from '@/components/uni-keyboard.vue'
export default {
	components: {
		uniPassword,
		uniIcon,
		uniKeyboard
	},
	data() {
		return {
			// 此页面数据
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			titleShowId: 'tabTag-0',
			tabHeight: 300,
			showKeyboard: false,
			// 提现数据
			arr:'',
		}
	},
	onLoad(option) {
		var pages = getCurrentPages();//当前页
		var beforePage = pages[pages.length - 2];//上个页面
		if(beforePage.route == 'pages/userPages/tiXian/tiXian'){
			this.arr = JSON.parse(option.arr)
		}else if(beforePage.route == 'pages/userPages/balance/balance'){
			this.arr = JSON.parse(option.arr)
		}
	},
	methods: {
		// 安全密码页面函数
		done(password) {
			console.log(password);
			console.log(this.$refs);
		},
		confirmPwd() {
			this.$refs.keyboard.show();
		},
		onInput(e) {
		},
		// 验证安全密码并调用支付提现等功能
		onConfirm(e) {
			var that = this;
			var pages = getCurrentPages();//当前页
			var beforePage = pages[pages.length - 2];//上个页面
			// 点击完成时触发
			let password = e.value;
			console.log(e.value);
			uni.request({
				url:that.http+'user/validationSafePass',
				header:that.header,
				method:'POST',
				data:{
					safe_pass:e.value,
					user_id:uni.getStorageSync('token')
				},
				success: (res) => {
					if(res.data.code == 200){
						// 提现执行体现功能
						if(beforePage.route == 'pages/userPages/tiXian/tiXian'){
							that.tx(that.arr)
						}else if(beforePage.route == 'pages/userPages/balance/balance'){
							that.cz(that.arr)
						}
					}else{
						uni.showToast({
							title:res.data.data,
							icon:'none'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!',
						icon:'none'
					})
				}
			})
		},
		tabChange(e) {
			var index = e.target.id.replace('tabTag-', '');
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		// 充值功能
		cz(obj){
			var that = this;
			console.log(obj)
		},
		// 提现功能
		tx(obj){
			var that = this;
			uni.showLoading({
				title:'正在提现...'
			})
			uni.request({
				url:that.http+'user/tixian',
				header:that.header,
				method:'POST',
				data:{
					type:2,		//1是网店2是用户
					id:uni.getStorageSync('token'),
					money:obj.money,
					card:obj.cardID,
					name:obj.name,
					way:obj.way
				},
				success: (res) => {
					uni.hideLoading()
					if(res.data.code==200){
						uni.showToast({
							title:res.data.data,
							icon:'none'
						})
					}else{
						uni.showToast({
							title:res.data.data,
							icon:'none'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!',
						icon:'none'
					})
				}
			})
		}
	}
}
